<!-- ALL THE HTML -->
<template>
  <div id="app">
    <div
        v-for="(item, index) in clouds"
        class="cloud"
        :id="'cloud-' + index"
        :style="makeStarStyles(index)"
        :key="index"
    ></div>
    <!--     <button class="testbtn" @click="changeDisplay">Fire!</button>-->
    <div id="holoLoader" :class="displayMode ? 'active' : 'notActive'">
      <nav class='menu'>
        <div class='menu-toggler' id='menu-toggler'></div>
        <label for='menu-toggler'></label>
        <ul>
          <li class='menu-item'>
            <a class='' href='#home' target='_self'>Home</a>
          </li>
          <li class='menu-item'>
            <a class='' href='#utility' target='_self'>Utility</a>
          </li>
          <li class='menu-item'>
            <a class='' href='#gallery' target='_self'>Gallery</a>
          </li>
          <li class='menu-item'>
            <a class='' href='#roadmap' target='_self'>Roadmap</a>
          </li>
          <li class='menu-item'>
            <a class='' href='#socialcause' target='_self'>Social cause</a>
          </li>
          <li class='menu-item'>
            <a class='' href='#ourchannels' target='_self'>Our channels</a>
          </li>
        </ul>
      </nav>
      <div class="holoLayer" id="core">
        <canvas id="coreCanvas" style="border-color: transparent;
    border-style: solid;
    border-width: 2px;
    position: absolute;
    top: 0;"/>
      </div>
      <div class="holoLayer" id="firstCircle">
        <canvas id="secondCanvas"></canvas>
      </div>
      <div class="holoLayer" id="secondCircle">
        <div class="slider a">
          <div class="sliderPoint a">
            <!--<div :key="item.toString()" v-for="item in sparks" class="spark" />-->
          </div>
        </div>
      </div>
      <div class="holoLayer" id="thirdCircle"></div>
      <div class="holoLayer" id="fourthCircle"></div>
      <div class="holoLayer" id="fifthCircle">
        <canvas id="firstCanvas"/>
      </div>
      <div class="holoLayer" id="sixthCircle"></div>
    </div>
  </div>
</template>
<!-- ALL THE JAVASRIPT -->
<script>
export default {
  data() {
    return {
      canvas: {
        core: null,
        first: null,
        second: null
      },
      clouds: 0,
      numberOfCoreParticles: 15,
      particles: [],
      ringNumber: 4,
      stylesArray: [],
      OuterRings: [],
      instances: 40,
      numberOfExternalRingSegments: 70,
      externalRingSegments: [],
      loadingAngleStart: 7.5,
      then: Date.now(),
      displayModeEngage: Date.now(),
      displayMode: false,
      angles: [
        [0, 25, 5, "#FF0dc1"],
        [50, 76, 10, "#FF0dc1"],
        [101, 126, 5, "#FF0dc1"],
        [152, 177, 10, "#FF0dc1"],
        [203, 227, 5, "#FF0dc1"],
        [258, 279, 10, "#FF0dc1"],
        [305, 334, 5, "#FF0dc1"],
      ],
    };
  },
  methods: {
    changeDisplay() {
      //this.displayMode = !this.displayMode
      this.displayModeEngage = Date.now();
      //console.log("changed display!")
    },
    loop() {

      this.update();
      this.render();
      requestAnimationFrame(this.loop);
    },
    update() {
      if (this.displayMode === false) {
        this.loadingAngleStart = this.loadingAngleStart + 0.2;
        this.loadingAngleStart >= 100 ? (this.changeDisplay(), (this.loadingAngleStart = 100)) : null;
      } else {
        this.loadingAngleStart = this.loadingAngleStart - 0.4;
        this.loadingAngleStart <= 0 ? (this.changeDisplay(), (this.loadingAngleStart = 0)) : null;
      }

      // eslint-disable-next-line no-unused-vars
      this.particles.forEach((item, index) => {
        this.adjustLength(item);
        this.moveCoreParticle(item);
      });

      // eslint-disable-next-line no-unused-vars
      this.OuterRings.forEach((item, index) => {
        this.updateRings(item);
      });
      // eslint-disable-next-line no-unused-vars
      this.externalRingSegments.forEach((item, index) => {
        this.updateCount(item);
      });
      //console.log("we updating!")
    },
    render() {
      let coreCanvas = document.getElementById("coreCanvas");
      let firstCanvas = document.getElementById("firstCanvas");
      let secondCanvas = document.getElementById("secondCanvas");
      let coreCanvasContext = coreCanvas.getContext("2d");
      let firstCanvasContext = firstCanvas.getContext("2d");
      let secondCanvasContext = secondCanvas.getContext("2d");
      coreCanvasContext.globalCompositeOperation = "screen"; //test */
      coreCanvasContext.clearRect(0, 0, coreCanvas.width, coreCanvas.height);

      // eslint-disable-next-line no-unused-vars
      this.particles.forEach((item, index) => {
        this.renderParticle(item, coreCanvasContext);
      });
      firstCanvasContext.clearRect(0, 0, firstCanvas.width, firstCanvas.height);
      this.drawCircularPlate(this.angles, firstCanvasContext);
      secondCanvasContext.clearRect(0, 0, secondCanvas.width, secondCanvas.height);
      // eslint-disable-next-line no-unused-vars
      this.externalRingSegments.forEach((item, index) => {
        this.drawCounter(item, secondCanvasContext);
      });
      this.drawShootTime(this.loadingAngleStart, secondCanvasContext);
      this.drawOuterRing(this.OuterRings, secondCanvasContext);

    },
    createExternalRingSegments(number) {
      for (let n = 0; n < number; n++) {
        let segment = {
          number: n,
          opacity: Math.random(),
          reverse: true,
          start: n * (360 / number),
          finish: (n + 1) * (360 / number),
          fill: "transparent",
          shadow: "rgba(255,255,255,0.5)",
          strokeStyle: "116,4,224",
          lineWidth: Math.floor(Math.random() * 5 + 13),
        };
        this.externalRingSegments.push(segment);
      }
    },
    drawShootTime(start, context) {
      const canvas = this.canvas["second"]
      let finish = (270 / 100) * start - 90;
      context.strokeStyle = `rgba(36, 188, 255, ${start / 100})`;
      context.beginPath();
      context.arc(
          canvas.width / 2,
          canvas.height / 2,
          canvas.width / 2 * 0.75,
          -90 * (Math.PI / 180),
          finish * (Math.PI / 180)
      );
      context.stroke();
      context.closePath();
      // // X = Math.cos((angle * Math.PI) / 180) * length + startPointX;
      // let calculateX = Math.cos(((finish + 5) * Math.PI) / 180) * canvas.width/2+ canvas.width/2*0.75 ;
      // //Y = Math.sin(angle*Math.PI/180) * length + startPointY
      // let calculateY = Math.sin(((finish + 5) * Math.PI) / 180) * canvas.height/2 +canvas.height/2*0.75;
      // context.fillStyle = "white";
      // context.textAlign = "center";
      // context.font = "10px Arial";
      // context.fillText(
      //     start.toString().substring(0, 2) + "%",
      //     calculateX,
      //     calculateY
      // );
    },
    updateCount(item) {
      if (item.reverse === true) {
        item.opacity -= 0.01;
        item.opacity < 0.1 ? (item.reverse = false) : null;
      }
      if (item.reverse === false) {
        item.opacity += 0.01;
        item.opacity > 0.9 ? (item.reverse = true) : null;
      }
    },
    drawCounter(item, canvasContext) {
      const canvas = this.canvas["second"]
      if (item.number % 2) {
        canvasContext.beginPath();
        canvasContext.strokeStyle = `rgba(${item.strokeStyle}, ${item.opacity})`;
        canvasContext.arc(
            canvas.width / 2,
            canvas.height / 2,
            canvas.width / 2 * 0.85 + item.lineWidth,
            item.start * (Math.PI / 180),
            item.finish * (Math.PI / 180)
        );
        canvasContext.fillStyle = item.fill;
        canvasContext.fill();
        canvasContext.shadowColor = item.shadow;
        canvasContext.shadowBlur = 8;
        canvasContext.lineWidth = item.lineWidth;
        canvasContext.strokeStyle = 1;
        canvasContext.stroke();
        canvasContext.closePath();
      }
    },
    createCoreParticles(number) {
      const coreCanvas = this.canvas["core"];
      for (let n = 0; n < number; n++) {
        let angle = Math.floor(Math.random() * 270);
        let ring = Math.floor(Math.random() * 75);
        let length = 5 + Math.random() * coreCanvas.width * 0.1;
        let speed = Math.random() - 1.2;

        if (speed > 0.3) {
          (speed = 0.3)
        }
        const light_blue = "rgba(146,34,254,0.7)"

        let particle = {
          x: coreCanvas.offsetWidth / 2 + (length + ring * Math.cos(angle * (Math.PI / 180))),
          y: coreCanvas.offsetHeight / 2 + (length + ring * Math.sin(angle * (Math.PI / 180))),
          radius: Math.random() * 2 + 3,
          startRadius: Math.random() * 2 + 12,
          length: length,
          speed: speed,
          usedLength: length,
          grow: Math.random() > 0.5,
          startAngle: angle,
          number: n,
          usedAngle: angle,
          ring: ring,
          color:
              Math.random() > 0.45 ? "rgba(255,255,255,1)" : light_blue,
          shadow: "rgba(255, 255, 255,1)",
        };
        this.particles.push(particle);
      }
    },
    makeStarStyles(cloudIndex) {
      if (document.getElementById("cloud-" + cloudIndex) === null) {
        let radius = Math.floor(Math.random() * 230 + 130);
        let x =
            "top:" +
            Math.floor(Math.random() * document.body.clientHeight - radius / 2) +
            "px";
        let y =
            "left:" +
            Math.floor(Math.random() * document.body.clientWidth - radius / 2) +
            "px";
        let animationTime = Math.floor(Math.random() * 10 + 5);
        let animationDelay = Math.floor(Math.random() * 15);
        let background =
            "background:radial-gradient(circle, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.05) 34%, rgba(217,217,217,0.0) 70%)";
        let style =
            "position:absolute;" +
            x +
            ";" +
            y +
            ";" +
            "height:" +
            radius +
            "px;" +
            "width:" +
            radius +
            "px;" +
            "border-radius:180px;" +
            background +
            ";animation-duration: " +
            animationTime +
            "s;animation-delay:" +
            animationDelay +
            "s;";
        this.stylesArray.push(style);
        //console.log(style)
        return style;
      } else {
        return this.stylesArray[cloudIndex];
      }
    },
    renderParticle(particle, canvas) {
      var grd = canvas.createRadialGradient(
          particle.x,
          particle.y,
          1,
          particle.x,
          particle.y,
          particle.radius - particle.radius / 10
      );
      grd.addColorStop(0, particle.color);
      grd.addColorStop(1, "rgba(255,255,255,0)");
      canvas.fillStyle = grd;
      canvas.beginPath();
      canvas.arc(particle.x, particle.y, particle.radius, 0, 2 * Math.PI);
      canvas.closePath();
      canvas.fill();
    },
    createRings(rings) {

      const canvas = this.canvas["second"]
      for (let i = 0; i < rings; i++) {

        let startAng = Math.floor(Math.random() * Math.random() * 420);
        let endAng = startAng + Math.random() * 80 + Math.random() * 50;
        //let distance = Math.floor(Math.random() * 50 + 50)

        let obj = {
          startAng: startAng * (Math.PI / 180),
          endAng: endAng * (Math.PI / 180),
          length: canvas.width * 0.25 + 5 * i,
          shown: true,
          moveDirection: Math.random() > 0.5,
          moveDone: false,
        };
        this.OuterRings.push(obj);
        // console.log(obj);
      }
    },
    updateRings(ring) {
      if (ring.moveDone === false) {
        ring.moveDirection
            ? ((ring.startAng -= 0.01), (ring.endAng -= 0.01))
            : ((ring.startAng += 0.01), (ring.endAng += 0.01));
        Math.random() < 0.02 ? (ring.moveDone = true) : null;
      } else {
        ring.moveDirection = !ring.moveDirection;
        ring.moveDone = false;
      }
    },
    drawOuterRing(points, canvasContext) {
      //multiples anillos (arcos) concentricos
      const canvas = this.canvas["second"];
      points.forEach((item) => {
        if (item.shown === true) {
          //console.log(item.startAng, item.endAng)
          canvasContext.beginPath();
          // console.log(`width outher ring ${canvas.width}`)
          canvasContext.arc(
              canvas.width / 2,
              canvas.width / 2,
              item.length + 5,
              item.startAng,
              item.endAng,
              0,
              2 * Math.PI
          );
          canvasContext.fillStyle = "transparent";
          canvasContext.fill();
          canvasContext.shadowColor = "rgba(175,7,82,1)";
          canvasContext.shadowBlur = 15;
          canvasContext.lineWidth = 6;
          canvasContext.strokeStyle = "#E62EE8";
          canvasContext.stroke();
          canvasContext.closePath();
        }
      });
    },
    adjustLength(particle) {
      particle.number === 0 ? particle.grow : null
      if (particle.grow && !this.displayMode) {
        particle.usedLength += Math.abs(particle.speed) / 2;
        particle.usedLength > particle.length + 20
            ? (particle.grow = false)
            : null;
        particle.radius > particle.startRadius
            ? ((particle.radius -= 0.5),
                (particle.usedLength += particle.radius / 3))
            : null;
      } else if (!particle.grow && !this.displayMode) {
        particle.usedLength -= Math.abs(particle.speed) / 2;
        particle.usedLength < particle.length - 20
            ? (particle.grow = true)
            : null;
        particle.radius > particle.startRadius ? (particle.radius -= 1) : null;
      } else if (this.displayMode) {
        !particle.grow ? (particle.grow = true) : null;
        particle.usedLength > 0
            ? (particle.usedLength -= particle.radius / 10)
            : null;
        particle.radius < 25 ? (particle.radius += 1) : null;
      }
    },
    moveCoreParticle(particle) {
      const canvasReference = this.canvas["core"];
      particle.usedAngle = particle.usedAngle + particle.speed;
      particle.x =
          canvasReference.height / 2 +
          particle.usedLength * Math.cos(particle.usedAngle * (Math.PI / 180));
      particle.y =
          canvasReference.width / 2 +
          particle.usedLength * Math.sin(particle.usedAngle * (Math.PI / 180));
    },
    drawCircularPlate(angles, context) {
      // platos concentricos con segmentos diferente grosor
      const canvas = this.canvas["first"]
      const xCenter = canvas.width / 2
      const yCenter = canvas.height / 2
      angles.forEach((item) => {
        context.beginPath();
        context.arc(
            xCenter,
            yCenter,
            yCenter - 5,
            item[0] * (Math.PI / 180),
            item[1] * (Math.PI / 180)
        );
        context.fillStyle = "transparent";
        context.fill();
        context.shadowColor = item[3];
        context.shadowBlur = 15;
        context.lineWidth = item[2];
        context.strokeStyle = item[3];
        context.stroke();
        context.closePath();
      });
      context.beginPath();
      context.arc(xCenter, yCenter, yCenter - 15, 0, 2 * Math.PI);
      context.fillStyle = "transparent";
      context.fill();
      context.shadowColor = "#FF0dc1";
      context.shadowBlur = 15;
      context.lineWidth = 2;
      context.strokeStyle = "rgba(175,7,82,1)";
      context.stroke();
      context.closePath();

      context.beginPath();
      context.arc(xCenter, yCenter, yCenter, 0, 2 * Math.PI);
      context.fillStyle = "transparent";
      context.fill();
      context.shadowColor = "#FF0dc1";
      context.shadowBlur = 15;
      context.lineWidth = 2;
      context.strokeStyle = "rgba(175,7,82,1)";
      context.stroke();
      context.closePath();
    }
  }
  ,
  mounted() {
    this.canvas["second"] = document.getElementById("secondCanvas")
    this.canvas["second"].height = this.canvas["second"].width

    let coreCanvas = document.getElementById("coreCanvas");
    this.canvas["core"] = coreCanvas;
    coreCanvas.width = document.getElementById("core").offsetWidth;
    coreCanvas.height = document.getElementById("core").offsetHeight;


    let firstCanvas = document.getElementById("firstCanvas");
    this.canvas["first"] = firstCanvas;
    firstCanvas.width = document.getElementById("firstCircle").offsetWidth;
    firstCanvas.height = document.getElementById("firstCircle").offsetHeight;

    let firstCanvasContext = firstCanvas.getContext("2d");
    this.createExternalRingSegments(this.numberOfExternalRingSegments);
    this.createCoreParticles(this.numberOfCoreParticles);
    this.createRings(this.ringNumber);
    this.drawCircularPlate(this.angles, firstCanvasContext);

    firstCanvas.width = document.getElementById("firstCircle").offsetWidth;
    firstCanvas.height = document.getElementById("firstCircle").offsetHeight;


    window.addEventListener(
        "resize",
        function () {
          let coreCanvas = document.getElementById("coreCanvas");
          coreCanvas.width = document.getElementById("core").offsetWidth;
          coreCanvas.height = document.getElementById("core").offsetHeight;

          let firstCanvas = document.getElementById("firstCanvas");
          firstCanvas.width = document.getElementById("firstCircle").offsetWidth;
          firstCanvas.height = document.getElementById("firstCircle").offsetHeight;
          firstCanvas.width = document.getElementById("firstCircle").offsetWidth;
          firstCanvas.height = document.getElementById("firstCircle").offsetHeight;
        },
        true
    );
    //Let's loadingAngleStart this!
    //we go!
    this.loop();
  }
  ,
}
;
</script>
<!-- ALL THE STYLES -->
<style>
:root {
  --main-blue-color: #13aeff;
  --main-white-color: #7be326;
  --main-orange-color: #0328d9;
}

#app {
  --main-blue-color: #13aeff;
  --main-white-color: #7be326;
  --main-orange-color: #0328d9;
  display: flex;
  width: 100vw;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(4, 5, 21);
  flex-direction: row;
  align-content: center;
  justify-content: center;
  align-items: baseline;
  position: fixed;
  z-index: 100000;
}


.cloud {
  z-index: -1;
  opacity: 0.5;
  animation: cloudFade;
}

@keyframes cloudFade {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}

@keyframes reverseRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}

#secondCircle .slider {
  /* position: absolute; */
  background-color: transparent;
  width: 50%;
  box-sizing: border-box;
  height: 50%;
  top: 50%;
  left: 50%;
  position: relative;
  -webkit-animation: rotate 12s infinite linear;
  animation: rotate 12s infinite linear;
  transform-origin: 4px 4px;
}

#secondCircle .sliderPoint.a {
  box-shadow: 0 0 3px 2px rgba(255, 255, 255, 0.55);
  left: 100%;
  width: 4px;
  background-color: rgba(255, 255, 255, 0.55);
  position: relative;
  box-sizing: border-box;
  border-radius: 100%;
  height: 4px;
}

#secondCircle .sliderPoint.a:before {
  content: "";
  box-shadow: 0 0 3px 2px rgba(255, 255, 255, 0.55);
  width: 4px;
  position: relative;
  top: 105px;
  left: -45px;
  border-radius: 100%;
  height: 4px;
  display: block;
  background-color: rgba(255, 255, 255, 0.55);
}

#secondCircle .sliderPoint.a:after {
  content: "";
  box-shadow: 0 0 3px 2px rgba(255, 255, 255, 0.55);
  width: 4px;
  position: relative;
  top: -85px;
  left: -25px;
  border-radius: 90px;
  height: 4px;
  display: block;
  background-color: rgba(255, 255, 255, 0.55);
}

#holoLoader.active .sliderPoint.a:after {
  animation: sparks 3s infinite linear 0s;
}

#holoLoader.active .sliderPoint.a:before {
  animation: sparks 3s infinite linear 1.2s;
}

#holoLoader.active .sliderPoint.a {
  animation: sparks 3s infinite linear 0.3s;
}

@keyframes sparks {
  0% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
  60% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
  61% {
    box-shadow: 0 0 5px 8px rgba(255, 255, 255, 0.75);
  }
  62% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
  84% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
  85% {
    box-shadow: 0 0 5px 8px rgba(255, 255, 255, 0.75);
  }
  86% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
}

canvas {
  width: 100%;
  height: 100%;
}

#holoLoader {
  z-index: 1;
  position: relative;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0);
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  transition: all 0.5s ease-in-out 0.4s;
  /*transform: perspective(1000px) rotateX(30deg) rotateY(-50deg) rotateZ(0deg);*/
}

.holoLayer {
  /*background: radial-gradient(circle, rgba(16, 29, 216, 0.219) 0%, rgba(1, 4, 187, 0.103) 65%, rgba(147,196,255,1) 1%);*/
  box-shadow: 0 0 39px 0 rgba(146, 34, 254, 0.15);
  border-radius: 360px;
  transition: all 0.5s ease-in-out 0s;
  z-index: -999999;
}

#holoLoader.active {
  transform: perspective(1000px) rotateX(30deg) rotateY(-50deg) rotateZ(120deg);
  transition: all 0.5s ease-in-out;
}

#core {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(23, 29, 231, 0.1) 0%, rgba(255, 255, 255, 0.0) 17%, rgba(147, 196, 255, 0.1) 24%, rgba(255, 255, 255, 0.0) 30%);
}

#coreCanvas,
#firstCanvas,
#secondCanvas {
  width: 100%;
  height: 100%;
}

#firstCircle {
  position: absolute;
  top: 0;
  width: 90%;
  height: 90%;
  margin: 5%;
}

@keyframes stutterMovement {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(50deg);
  }
  30% {
    transform: rotate(50deg);
  }
  40% {
    transform: rotate(50deg);
  }
  45% {
    transform: rotate(70deg);
  }
  60% {
    transform: rotate(-20deg);
  }
  70% {
    transform: rotate(0deg);
  }
  85% {
    transform: rotate(20deg);
  }
  90% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(45deg);
  }
}

#holoLoader.active #firstCircle {
  transform: translateX(-130px) translateY(-80px);
  transition: all 0.5s ease-in-out 0.4s;
}

#secondCircle {
  position: absolute;
  top: 0;
  width: 80%;
  height: 80%;
  margin: 10%;
  border: 1px solid var(--main-orange-color);
}

#secondCircle:before {
  position: absolute;
  transition: all 0.5s ease-in-out 0s;
  content: "";
  border: 7px dotted rgba(79, 223, 255, 0.3);
  top: 0;
  width: 100%;
  height: 100%;
  left: 0;
  border-radius: 360%;
  opacity: 0;
}

#secondCircle:after {
  position: absolute;
  transition: all 0.5s ease-in-out 0s;
  content: "";
  border: 5px solid rgba(79, 223, 255, 0.7);
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 360%;
  opacity: 0;
}


#holoLoader.active #secondCircle {
  border: 2px solid rgba(175, 7, 82, 0.5);
  transform: translateX(-180px) translateY(-120px);
  transition: all 0.5s ease-in-out 0.4s;
}

#holoLoader.active #secondCircle:before {
  opacity: 1;
  top: -55px;
  left: -75px;
  transition: all 0.5s ease-in-out 0.4s;
  animation: rotation 5s linear 0s infinite;
}

@keyframes rotation {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}

#holoLoader.active #secondCircle:after {
  opacity: 1;
  transform: translateX(45px) translateY(35px);
  transition: all 0.5s ease-in-out 0.4s;
}

#thirdCircle {
