}

#fifthCircle {
  position: absolute;
  top: 0;
  width: 50%;
  height: 50%;
  margin: 25%;
  animation: stutterMovement 5s infinite linear;
}

#holoLoader.active #fifthCircle {
  /*transform: translateX(-600px) translateY(-400px);*/
