}

#sixthCircle {
  position: absolute;
  top: 0;
  width: 40%;
  height: 40%;
  margin: 30%;
  border: 1px dashed var(--main-orange-color);
  border-radius: 180%;
}

#holoLoader.active #sixthCircle {
  border: 3px solid var(--main-white-color);
  transition: all 0.5s ease-in-out 0.4s;
}

#holoLoader.active #sixthCircle:before {
  position: absolute;
  width: 40%;
  height: 40%;
  margin: 30%;

  border: 1px dashed var(--main-white-color);
  border-radius: 180%;
  transition: all 0.5s ease-in-out 0s;
  animation: bubbleEffect 4s infinite linear;
  box-shadow: 0 0 0 rgba(255, 255, 255, 0);
}

#sixthCircle:before {
  position: absolute;
  content: "";
  border-radius: 180%;
  width: 20%;
  display: block;
  height: 20%;
  margin: 40%;

  border: 1px solid var(--main-white-color);
  box-shadow: 0 0 5px 2px rgba(142, 1, 61, 255),
  inset 0 0 5px rgb(142, 1, 61);
  transition: all 0.5s ease-in-out 0.4s;
  animation: rotationY 4s infinite linear;
}

#holoLoader.active #sixthCircle:after {
  position: absolute;
  width: 40%;
  height: 40%;
  margin: 30%;
  top: -5px;
  left: -5px;
  border: 1px dashed var(--main-white-color);
  border-radius: 180%;
  transition: all 0.5s ease-in-out 0s;
  animation: bubbleEffect 4s infinite linear;
  box-shadow: 0 0 0 rgba(141, 42, 42, 0);
}

#sixthCircle:after {
  position: absolute;
  content: "";
  display: block;
  width: 20%;
  height: 20%;
  border-radius: 180%;
  margin: 40%;
  top: -5px;
  left: -5px;
  box-shadow: 0 0 5px 2px var(--main-orange-color), inset 0 0 5px var(--main-orange-color);
  border: 1px solid var(--main-white-color);
  transition: all 0.5s ease-in-out 0.4s;
  animation: rotationX 8s infinite linear;
}

@keyframes rotationX {
  0% {
    transform: rotateX(0deg) rotateY(0deg) scale(1);
  }
  100% {
    transform: rotateX(360deg) rotateY(-360deg) scale(1);
  }
}

@keyframes rotationY {
  0% {
    transform: rotateY(0deg) rotateX(0deg) scale(1);
  }
  100% {
    transform: rotateY(360deg) rotateX(360deg) scale(1);
  }
}

@keyframes bubbleEffect {
  0% {
    animation-timing-function: linear;
    transform: scale(1);
  }
  25% {
    animation-timing-function: linear;
    opacity: 1;
    transform: scale(1.2);
  }
  50% {
    animation-timing-function: linear;
    opacity: 0.5;
    transform: scale(1);
  }
  75% {
    animation-timing-function: linear;
    opacity: 0.5;
    transform: scale(0.8);
  }
  100% {
    animation-timing-function: linear;
    opacity: 1;
    transform: scale(1);
  }
}

.menu-toggler {
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 40px;
  height: 40px;
  z-index: 2;
  opacity: 0;
}

.menu-toggler + label {
  width: 40px;
  height: 5px;
  display: block;
  z-index: 1;
  border-radius: 2.5px;
  background: rgba(255, 255, 255, 0.7);
  transition: transform 0.5s, top 0.5s;
  position: absolute;
  opacity: 0;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}

.menu-toggler + label:before, .menu-toggler + label:after {
  width: 40px;
  height: 5px;
  display: block;
  z-index: 1;
  border-radius: 2.5px;
  background: rgba(255, 255, 255, 0.7);
  transition: transform 0.5s, top 0.5s;
  content: "";
  position: absolute;
  left: 0;
}

.menu-toggler + label:before {
  top: 10px;
}

.menu-toggler + label:after {
  top: -10px;
}

.menu-toggler:hover + label, .menu-toggler:hover + label:before, .menu-toggler:hover + label:after {
  background: white;
}


.menu-item {
  font-family: "Therhog", sans-serif;
  font-size: 3rem;
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 80px;
  height: 80px;
  opacity: 0;
  transition: 0.5s;
}


.menu-item a {
  font-family: "Therhog", sans-serif;
  font-size: 3.2rem;
  line-height: 2rem;
  display: block;
  width: inherit;
  /*line-height: 5px;*/
  color: rgba(255, 255, 255, 1);

  border-radius: 50%;
  text-align: center;
  text-decoration: none;
  pointer-events: none;
  transition: 0.2s;
}


.menu-item:nth-child(1) a {
  transform: rotate(0deg);
}

.menu-item:nth-child(2) a {
  transform: rotate(-60deg);
}

.menu-item:nth-child(3) a {
  transform: rotate(-120deg);
}

.menu-item:nth-child(4) a {
  transform: rotate(-180deg);
}

.menu-item:nth-child(5) a {
  transform: rotate(-240deg);
}

.menu-item:nth-child(6) a {
  transform: rotate(-300deg);
}

.menu-item a:hover {
  text-shadow: 0 0 5px #fff,
  0 0 10px #fff,
  0 0 20px #fff,
  0 0 40px #0ff,
  0 0 80px #0ff,
  0 0 90px #0ff,
  0 0 100px #0ff,
  0 0 150px #0ff;
}

.menu-toggler + label:before, .menu-toggler + label:after {
  top: 0;
  width: 40px;
  transform-origin: 50% 50%;
}

.menu-toggler + label:before {
  transform: rotate(45deg);
}

.menu-toggler + label:after {
  transform: rotate(-45deg);
}

.menu-toggler ~ ul .menu-item {
  opacity: 1;
}

.menu-toggler ~ ul .menu-item:nth-child(1) {
  transform: rotate(0deg) translateX(-40vmin);
}

.menu-toggler ~ ul .menu-item:nth-child(2) {
  transform: rotate(60deg) translateX(-40vmin);
}

.menu-toggler ~ ul .menu-item:nth-child(3) {
  transform: rotate(120deg) translateX(-40vmin);
}

.menu-toggler ~ ul .menu-item:nth-child(4) {
  transform: rotate(180deg) translateX(-40vmin);
}

.menu-toggler ~ ul .menu-item:nth-child(5) {
  transform: rotate(240deg) translateX(-40vmin);
}

.menu-toggler ~ ul .menu-item:nth-child(6) {
  transform: rotate(300deg) translateX(-40vmin);
}

.menu-toggler ~ ul .menu-item a {
  pointer-events: auto;
}


